<template>
  <div>
    <cms-breadcrumb to="/role" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>可进行添加修改角色管理员，维护管理员权限</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
           <el-form-item label="角色名" prop="roleName" :error='errorMessage.roleName'>
              <el-input v-model="formData.roleName" :autofocus="true" ref="first"></el-input>
          </el-form-item>
          <el-form-item label="描述" >
              <el-input maxlength="500" v-model="formData.remark" type="textarea" :autosize="{ minRows: 3}"></el-input>
          </el-form-item>
         <el-form-item label="权限设置">
              <role-tree ref='role' :menuIds='formData.menuid' v-if="roleStatus"></role-tree>
          </el-form-item>
      </el-form>
       <div class="cms-form-footer">
        <cms-button  @click="save(false)" v-color >保存并新增</cms-button>
        <cms-button @click="save(true)"  v-color>保存</cms-button>
        <cms-button type='rest' @click="restData">重置</cms-button> 
      </div>
    </div>
  </div>
</template>

<script>
import roleTree from './roleTree'
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { coreRoleSave } from '@/api/admin/role.js'
var defaultData = {
  'roleName': '',
  'remark': '',
  'menuid': []
}
export default {
  name: 'roleSave',
  components: {
    roleTree
  },
  mixins: [form],
  data() {
    return {
      roleStatus: false, // 权限树手动重载
      formData: Object.assign({}, defaultData),
      rules: {
        roleName: [rules.required('请填写角色名'), rules.len(1, 10)],
        remark: [rules.max(140, '描述最大长度为140个字符')]
      }
    }
  },
  methods: {
    save(state) {
      this.formData.menuid = this.$refs.role.getPermsInfo().map(v => v.id)
      this.saveForm(coreRoleSave, this.formData, state, '/role', 'list').then(res => {
        this.formData.menuid = []
        this.roleStatus = false
        setTimeout(() => { // 重载过
          this.roleStatus = true
        }, 1)
      })
    },
    restData() {
      this.loading = true
      this.roleStatus = false
      setTimeout(() => {
        this.roleStatus = true
        this.formData = Object.assign({}, defaultData)
        this.loading = false
      }, 1)
    }
  },
  created() {
    this.restData()
  }

}
</script>

<style>
</style>



